<template>
  <div id="dashboard" class="dashboard" ref="dashboard">
      <!-- 第一部分 -->
      <el-row class="main-content" :gutter="10">
        <el-col span="12">
          <el-card shadow="hover" class="todo-wrap">
            <div slot="header" class="todo-header">
              <span @click="chooseHandle('core', 1)" :class="{header_choose:1 === coreChoose}">应用模块统计</span>
              <span class="header_gang">|</span>
              <span @click="chooseHandle('core', 2)" :class="{header_choose:2 === coreChoose}">应用实例指标统计</span>
            </div>
            <div class="todo-list">
                <div ref="coreBarEcharts" v-if="isCore" key="coreTrue" v-cardSmallResize="162"></div>
                <div class="no-data" v-else key="coreFalse" v-cardSmallResize="162">暂无数据</div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card shadow="hover" class="todo-wrap">
            <div slot="header" class="todo-header">
              <span @click="chooseHandle('zipkin', 1)" :class="{header_choose:1 === zipkinChoose}">调用链耗时排序</span>
              <span class="header_gang">|</span>
              <span @click="chooseHandle('zipkin', 2)" :class="{header_choose:2 === zipkinChoose}">调用链出错率排序</span>
            </div>
            <div class="todo-list"  v-loading="loadingZipkin" element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading">
                <div ref="zipkinBarEcharts" v-if="isZipkin" key="zipkinTrue" v-cardSmallResize="162"></div>
                <div class="no-data" v-else key="zipkinFalse" v-cardSmallResize="162">暂无数据</div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <!-- 第二部分 -->
      <el-row class="main-content" :gutter="20">
        <el-col :span="8">
          <el-card shadow="hover" class="todo-wrap">
            <div slot="header" class="todo-header">
              <span>服务器指标排序</span>
            </div>
            <div class="todo-list">
                <div ref="serverBarEcharts" v-if="isServer" key="serverTrue" v-cardSmallResize="115"></div>
                <div class="no-data" v-else key="serverFalse" v-cardSmallResize="115">暂无数据</div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card shadow="hover" class="todo-wrap">
            <div slot="header" class="todo-header">
              <span>告警类型统计</span>
            </div>
            <div class="todo-list" v-loading="loadingAlarmType" element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading">
                <div ref="alarmPieEcharts" v-if="isAlarmType" key="alarmTypeTrue" v-cardSmallResize="115"></div>
                <div class="no-data" v-else key="alarmTypeFalse" v-cardSmallResize="115">暂无数据</div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card shadow="hover" class="todo-wrap">
            <div slot="header" class="todo-header">
              <span>告警级别统计</span>
            </div>
            <div class="todo-list" v-loading="loadingAlarmId" element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading">
                <div ref="alarmBarEcharts" v-if="isAlarmId" key="alarmIdTrue" v-cardSmallResize="115"></div>
                <div class="no-data" v-else key="alarmIdFalse" v-cardSmallResize="115">暂无数据</div>
            </div>
          </el-card>
        </el-col>
      </el-row>
  </div>
</template>

<script>
import echarts from '../../static/lib/echarts.min.js'
import Api from '@/constants/Api.js'
  export default {
    data() {
      return {
          coreChoose: 1,
          zipkinChoose: 1,
          loadingCore: false,
          isCore: true,
          coreBarData: [],
          loadingZipkin: false,
          isZipkin: true,
          zipkinOriginData: [],
          zipkinBarData: {},
          loadingServer: false,
          isServer: true,
          serverBarData: [],
          loadingAlarmType: false,
          isAlarmType: true,
          alarmPieData: [],
          loadingAlarmId: false,
          isAlarmId: true,
          LevelLogData: []
      }
    },
    directives: {
        cardSmallResize: {
            inserted(el, binding) {
                let num = Number(binding.value)
                el.style.height = Math.trunc(100 * el.clientWidth / num) + 'px'
                el.style.lineHeight = Math.trunc(100 * el.clientWidth / num) + 'px'
                window.addEventListener('resize', () => {
                    el.style.lineHeight = Math.trunc(100 * el.clientWidth / num) + 'px'
                    el.style.height = Math.trunc(100 * el.clientWidth / num) + 'px'
                }, false)
            }
        }
    },
    computed: {
    },
    watch: {
        coreBarData(newVal) {
            if (this.isCore && newVal) {
                // this.coreBarHandle(newVal)
                setTimeout(() => {
                    this.coreBarHandle(newVal)
                }, 1000)
            } else {
              this.isCore = false
            }
        },
        zipkinBarData(newVal) {
            if (this.isZipkin && JSON.stringify(newVal) !== '{}') {
                // this.zipkinBarHandle(newVal)
                setTimeout(() => {
                    this.zipkinBarHandle(newVal)
                }, 1000)
            } else {
                this.isZipkin = false
            }
        },
        serverBarData(newVal) {
            if (this.isServer && JSON.stringify(newVal) !== '{}') {
                setTimeout(() => {
                    this.serverBarHandle(newVal)
                }, 1000)
            } else {
               this.isServer = false
            }
        },
        alarmPieData(newVal) {
            if (this.isAlarmType && Array.isArray(newVal) && newVal.length > 0) {
                setTimeout(() => {
                    this.alarmPieHandle(newVal)
                }, 1000)
            } else {
                this.isAlarmType = false
            }
        },
        LevelLogData(newVal) {
            if (this.isAlarmId && Array.isArray(newVal) && newVal.length > 0) {
                let box = {
                    value: [],
                    name: [],
                    num: [],
                    model: []
                }
                newVal.forEach((value) => {
                    value.value && box.value.push(value.value)
                    value.name && box.name.push(value.name)
                    box.num.push(value.num)
                    box.model.push(1)
                })
                setTimeout(() => {
                    this.alarmBarHandle(box)
                }, 1000)
            } else {
                this.isAlarmId = false
            }
        }
    },
    mounted() {
        this.coreDataHandle()
        this.zipkinDataHandle()
        this.serverDataHandle()
        this.alarmTypeHandle()
        this.alarmIdHandle()
    },
    methods: {
        chooseHandle(title, num) {
            if (title === 'core') {
                this.coreChoose = num
            } else {
                this.zipkinChoose = num
            }
        },
        choosezipkinHandle(title, num) {
            if (title === 'core') {
                this.coreChoose = num
            } else {
                this.zipkinChoose = num
            }
        },
        coreDataHandle() {
            this.loadingAlarmType = false
            this.isCore = true
            this.coreBarData = [{
                    value: 1000,
                    name: '核心应用实例'
                },
                {
                    value: 850,
                    name: 'Zookeeper应用'
                },
                {
                    value: 450,
                    name: 'Redis应用'
                },
                {
                    value: 750,
                    name: '其他'
                }
            ]
        },
        zipkinDataHandle() {
            this.loadingZipkin = true
            this.isZipkin = true
            this.zipkinBarData = {}
            let endTime = (new Date()).getTime()
            let startTime = endTime - 604800000
            this.$axios.get(Api.spanDuration, {
                params: {startTime, endTime}
            }).then((res) => {
                this.zipkinOriginData = JSON.parse(JSON.stringify(res))
                let links = res
                if (Array.isArray(links) && links.length <= 0) {
                    this.loadingZipkin = false
                    this.isZipkin = false
                    this.zipkinBarData = {}
                    return false
                }
                this.loadingZipkin = false
                this.isZipkin = true
                let box = {
                    type: 'time',
                    name: [],
                    data: []
                }
                if (links.length > 10) {
                    links = links.slice(0, 10)
                }
                links.forEach((item) => {
                    box.name.push(item.spanName)
                    box.data.push(Math.trunc(item.avgDuration / 1000))
                })
                this.zipkinBarData = box
            }).catch((error) => {
                this.loadingZipkin = false
                this.isZipkin = false
                this.zipkinBarData = {}
                this.$message.error(error.data.message || error.message || error)
            })
            // this.zipkinBarData = {
            //     name: ['aaaaaaaaaaa', 'bbbbbbbbbb', 'ccccccccccccc', 'ddddddddddd', 'ffffffffff', 'ggggggggggg', 'hhhhhhhhhhhh', 'iiiiiiii'],
            //     data: [40.0, 35.0, 33.0, 30.0, 26.0, 24.0, 14.0, 12.0]
            // }
        },
        serverDataHandle() {
            this.loadingServer = false
            this.isServer = true
            this.serverBarData = {
                name: ['CPU', 'GPU', 'IO', '主板', '硬盘', '内存'],
                data: [
                    {value: 51}, {value: 34}, {value: 60}, {value: 78}, {value: 60}, {value: 90}
                ]
            }
        },
        alarmTypeHandle() {
            this.alarmPieData = []
            this.loadingAlarmType = true
            this.isAlarmType = true
            this.$axios.get(Api.getAlarmTypeLog).then(res => {
                this.isAlarmType = true
                this.loadingAlarmType = false
                this.alarmPieData = res.data
            }).catch(error => {
                this.loadingAlarmType = false
                this.isAlarmType = false
                this.alarmPieData = []
                this.$message.error(error.data.message || error.message || error)
            })
        },
        alarmIdHandle() {
            let vm = this
            vm.LevelLogData = []
            vm.loadingAlarmId = true
            vm.isAlarmId = true
            vm.$axios.get(Api.alarmLevelLog).then((res) => {
                this.isAlarmId = true
                this.loadingAlarmId = false
                this.LevelLogData = res.data
            }).catch((error) => {
                this.isAlarmId = false
                this.loadingAlarmId = false
                this.LevelLogData = []
                vm.$message.error(error.data.message || error.message || error)
            })
        },
        // 初始化ecahrts
        initEcharts(id, option) {
            // 基于准备好的dom，初始化echarts实例
            let ids = this.$refs[id]
            this.$echarts.init(ids).setOption(option)
            window.addEventListener('resize', () => {
                this.$echarts.init(ids).resize()
            }, false)
        },
        coreBarHandle(data) {
            let option = {
                tooltip: {
                    trigger: 'item',
                    formatter: '{a} <br/>{b} : {c} ({d}%)'
                },
                legend: {
                    orient: 'vertical',
                    left: '59%', // 控制lengend距离玫瑰图距离
                    y: 'center',
                    x: 'center',
                    icon: 'rect',
                    textStyle: {
                        color: [],
                        fontSize: 12,
                        width: 200,
                        height: 20
                    },
                    itemWidth: 10,
                    itemHeight: 10,
                    itemGap: 20,
                    formatter: function(name) {
                        let oa = option.series[0].data
                        // let total = 0
                        // oa.forEach((item, index) => {
                        //     total += item.value
                        // })
                        for (let i = 0; i < oa.length; i++) {
                            if (name === oa[i].name) {
                                // return name + '  ' + oa[i].value + '个 ' + '  ' + (oa[i].value / total * 100).toFixed(2) + '%'
                                return name + '  ' + oa[i].value + '个 '
                            }
                        }
                    }
                },
                color: ['#218FF0', '#FFCB3F', '#FA7782', '#31DAB3'],
                // color:viewColor,
                calculable: false,
                series: [{
                    name: '年龄',
                    type: 'pie',
                    radius: ['35%', '55%'], // 第一个参数控制玫瑰图圆心大小,第二个参数控制玫瑰图大小
                    center: ['23%', '50%'],
                    roseType: 'radius',
                    width: '100%', // for funnel
                    max: 100, // for funnel
                    itemStyle: {
                        normal: {
                            label: {
                                show: false
                            },
                            labelLine: {
                                show: false
                            }
                        },
                        emphasis: {
                            label: {
                                show: false
                            },
                            labelLine: {
                                show: false
                            }
                        }
                    },
                    data: data
                }]
            }
            this.initEcharts('coreBarEcharts', option)
        },
        zipkinBarHandle(zipkinBarData) {
            let names = zipkinBarData.name
            let name = zipkinBarData.name.map((item) => {
                return item.slice(0, 4) + '...'
            })
            let data = zipkinBarData.data
            let bigData = zipkinBarData.data[0]
            let leftSize = null
            for (let i = 1; i < zipkinBarData.data.length; i++) {
                if (zipkinBarData.data[i] > bigData) {
                    bigData = zipkinBarData.data[i]
                }
            }
            bigData = Math.trunc(bigData * 12 / 10)
            leftSize = (21 + (bigData + '').length * 8) + ''
            let option = {
                grid: {
                    top: '35',
                    left: leftSize,
                    right: '1%',
                    bottom: '50'
                    // containLabel: true
                },
                tooltip: {
                    show: true,
                    trigger: 'axis',
                    formatter: function(params) {
                        let ck = null
                        let numValue = params[0].value
                        let index = data.indexOf(numValue)
                        ck = names[index] + '：' + numValue
                        return ck
                    },
                    showDelay: 0,
                    hideDelay: 50,
                    transitionDuration: 0,
                    // backgroundColor: 'rgba(50,50,50,1)',
                    // borderColor: '#aaa',
                    showContent: true,
                    borderRadius: 8,
                    padding: 10
                },
                legend: {
                    data: ['调用链']
                },
                xAxis: [
                    {
                        type: 'category',
                        gridIndex: 0,
                        data: name,
                        axisPointer: {
                            type: 'shadow'
                        },
                        axisTick: {
                            length: '0'
                        },
                        axisLabel: {
                            rotate: 270
                        },
                        axisLine: {
                            show: false
                        }
                    }
                ],
                yAxis: [
                    {
                        type: 'value',
                        gridIndex: 0,
                        name: '单位：ms',
                        offset: '10',
                        min: 0,
                        max: bigData,
                        // interval: 10,
                        axisLabel: {
                            formatter: '{value}'
                        },
                        axisTick: {
                            length: '0'
                        },
                        axisLine: {
                            show: false
                        },
                        splitLine: {
                            show: true,
                            lineStyle: {
                                color: '#E6E6E6 '
                                // type: 'dashed'
                            }
                        }
                    }

                ],
                series: [
                    {
                        name: '耗时',
                        type: 'bar',
                        barWidth: '20%',
                        itemStyle: {
                            normal: {
                                // color: '#17C4FF'
                                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                    color: '#17C4FF'
                                }, {
                                    offset: 1,
                                    color: '#218FF0'
                                }])
                                // barBorderRadius: [5, 5, 0, 0]
                            }
                        },
                        data: data
                    }
                ]
            }
            this.initEcharts('zipkinBarEcharts', option)
        },
        serverBarHandle(serverBarData) {
            let data = serverBarData.data
            let titlename = serverBarData.name
            let dataBackGround = []
            for (let i = 0; i < data.length; i++) {
                dataBackGround.push(100)
            }
            data.map((value) => {
                if (value.value >= 90) {
                    value.itemStyle = {
                        normal: {
                            color: {
                                colorStops: [{
                                    offset: 0,
                                    color: '#DB3F4C'// 0% 处的颜色
                                }, {
                                    offset: 1,
                                    color: '#F5939F' // 100% 处的颜色
                                }],
                                globalCoord: false
                            }
                        }
                    }
                } else {
                    value.itemStyle = {
                        normal: {
                            color: {
                                colorStops: [{
                                    offset: 0,
                                    color: '#218FF0'// 0% 处的颜色
                                }, {
                                    offset: 1,
                                    color: '#45C2F9' // 100% 处的颜色
                                }],
                                globalCoord: false
                            }
                        }
                    }
                }
                return value
            })
            let option = {
                grid: {
                    top: '3%',
                    left: '40',
                    right: '40',
                    bottom: '0'
                    // containLabel: true
                },
                backgroundColor: '#fff',
                xAxis: {
                    show: false
                },
                yAxis: [
                    {
                        type: 'category',
                        show: true,
                        data: titlename,
                        inverse: true,
                        axisLine: {
                            show: false
                        },
                        splitLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLabel: {
                            color: '#000',
                            formatter: function(value, index) {
                                return [
                                    value
                                ].join('\n')
                            }
                        }
                    },
                    {
                        show: true,
                        inverse: true,
                        data: data,
                        axisLabel: {
                            textStyle: {
                                fontSize: 12,
                                color: '#000'
                            },
                            formatter: function(value, index) {
                                return value + '%'
                            }
                        },
                        axisLine: {
                            show: false
                        },
                        splitLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        }
                    }
                ],
                series: [
                    {
                        name: '条',
                        type: 'bar',
                        yAxisIndex: 0,
                        data: data,
                        barWidth: '15',
                        label: {
                            normal: {
                                show: false,
                                position: 'inside',
                                formatter: '{c}%'
                            }
                        }
                    },
                    {
                        show: true,
                        type: 'bar',
                        barGap: '-100%',
                        barWidth: '15', // 统计条宽度
                        itemStyle: {
                            normal: {
                                // barBorderRadius: 20,
                                color: '#F2F2F2'
                            }
                        },
                        z: 1,
                        data: dataBackGround
                    }
                ]
            }
            this.initEcharts('serverBarEcharts', option)
        },
        alarmPieHandle(alarmPieData) {
            // let xData = ['告警类型1', '告警类型2', '告警类型3', '告警类型4']
            // let data = [
            //     {value: 348, name: '告警类型1'},
            //     {value: 251, name: '告警类型2'},
            //     {value: 147, name: '告警类型3'},
            //     {value: 102, name: '告警类型4'}
            // ]
            let data = alarmPieData
            let option = {
                tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b}: {c} ({d}%)'
                },
                legend: {
                    // orient: 'vertical',
                    x: 'center',
                    y: 'top',
                    top: '71%',
                    itemWidth: 10,
                    itemHeight: 10
                },
                color: ['#218FF0', '#FFCB3F', '#FA7782', '#31DAB3'],
                series: [
                    {
                        name: '比例',
                        type: 'pie',
                        radius: ['25%', '41%'],
                        center: ['50%', '40%'],
                        label: {
                            normal: {
                                formatter: '{b|{b}} {c}个',
                                // backgroundColor: '#eee',
                                // borderColor: '#aaa',
                                // borderWidth: 1,
                                borderRadius: 4,
                                rich: {
                                    b: {
                                        fontSize: 12,
                                        lineHeight: 20
                                    }
                                }
                            }
                            // emphasis: {
                            //     show: true,
                            //     textStyle: {
                            //         fontSize: '30',
                            //         fontWeight: 'bold'
                            //     }
                            // }
                        },
                        data: data
                    }
                ]
            }
            this.initEcharts('alarmPieEcharts', option)
        },
        alarmBarHandle(data) {
            let id = data.value
            let zongjine = data.num
            let city = data.name
            let grayBar = data.model
            let fontSize = 12
            let bigData = data.num[0]

            for (let i = 0; i < data.num.length; i++) {
                if (data.num[i] > bigData) {
                    bigData = data.num[i]
                }
            }
            bigData = bigData * 12 / 10

            let colorBox = new Map([
                ['DEADLY', {
                    begin: '#DB3F4C',
                    end: '#F5939F'
                }],
                ['ERROR', {
                    begin: '#EC7537',
                    end: '#FBC08C'
                }],
                ['WARNING', {
                    begin: '#FFBE38',
                    end: '#FFE17A '
                }],
                ['INFO', {
                    begin: '#218FF0',
                    end: '#5BCDFF'
                }]
            ])
            let _zongjine = []
            function colorChoose(index, title) {
                if (title === 'begin') {
                    return (colorBox.get(id[index]) && colorBox.get(id[index]).begin) || '#218FF0'
                } else {
                    return (colorBox.get(id[index]) && colorBox.get(id[index]).end) || '#5BCDFF'
                }
            }
            zongjine.forEach((value, index) => {
                _zongjine.push({
                    name: city[index],
                    value: value,
                    itemStyle: {
                        normal: {
                            color: {
                                colorStops: [{
                                    offset: 0,
                                    color: colorChoose(index, 'begin')
                                }, {
                                    offset: 1,
                                    color: colorChoose(index, 'end')
                                }],
                                globalCoord: false
                            }
                        }
                    }
                })
            })
            let option = {
                grid: {
                    left: '0', // 如果离左边太远就用这个......
                    top: '10',
                    right: '0',
                    bottom: '0'
                    // containLabel: true
                },
                backgroundColor: '#fff',
                color: ['#559fef'],
                xAxis: [
                    {
                        show: false
                    },
                    // 由于下边X轴已经是百分比刻度了,所以需要在顶部加一个X轴,刻度是金额,也隐藏掉
                    {
                        show: false,
                        max: bigData
                    }
                ],
                yAxis: [{
                    type: 'category',
                    show: false,
                    axisLabel: {
                        inside: true
                    },
                    data: city
                }],
                series: [
                    // 数据--------------------我是分割线君------------------------------//
                    {
                        show: true,
                        type: 'bar',
                        barGap: '-100%',
                        barWidth: 15, // 统计条宽度
                        xAxisIndex: 1,
                        itemStyle: {
                            normal: {
                            // barBorderRadius: 20 // 统计条弧度
                            }
                        },

                        max: 1,
                        labelLine: {
                            show: false
                        },
                        z: 100,
                        data: _zongjine,
                        formatter: function(params) {
                            return params.value
                        }
                    },
                    // 右边数据条--------------------我是分割线君------------------------------//
                    {
                        show: true,
                        type: 'bar',
                        // xAxisIndex: 1, //代表使用第二个X轴刻度!!!!!!!!!!!!!!!!!!!!!!!!
                        barGap: '-100%',
                        barWidth: 15, // 统计条宽度
                        itemStyle: {
                            normal: {
                                // barBorderRadius: 20,
                                color: '#ecf0f4'
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                // label 的position位置可以是top bottom left,right,也可以是固定值
                                // 在这里需要上下统一对齐,所以用固定值
                            position: ['80%', '-180%'],
                                rich: {
                                    aa: {
                                        color: '#000',
                                        fontSize: fontSize
                                    }
                                },
                                formatter: function(data) {
                                    // const series1 = option.series[0].data
                                    let str = ''
                                    str += '{aa| ' + zongjine[data.dataIndex] + '个}'
                                    return str
                                }
                            }
                        },
                        z: 3,
                        formatter: function(params) {
                            return ''
                        },
                        data: grayBar
                    },

                    // 背景色--------------------我是分割线君------------------------------//
                    {
                        show: true,
                        type: 'bar',
                        barGap: '-100%',
                        barWidth: 15, // 统计条宽度
                        itemStyle: {
                            normal: {
                                color: '#F2F2F2'
                            }
                        },
                        label: {
                            normal: {
                                show: false,
                                position: ['60%', '-180%'],
                                fontSize: fontSize,
                                textStyle: {
                                    color: '#000'
                                }
                            }
                        },
                        data: grayBar

                    },
                    // 左边数据条--------------------我是分割线君------------------------------//
                    {
                        show: true,
                        type: 'bar',
                        xAxisIndex: 1, // 代表使用第二个X轴刻度!!!!!!!!!!!!!!!!!!!!!!!!
                        barGap: '-100%',
                        barWidth: 15, // 统计条宽度
                        itemStyle: {
                            normal: {
                                // barBorderRadius: 20,
                                color: '#ecf0f4'
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                position: [0, '-180%'],
                                fontSize: fontSize,
                                textStyle: {
                                    color: '#000'
                                },
                                formatter: function(data) {
                                    // 富文本固定格式{colorName|这里填你想要写的内容}
                                    return city[data.dataIndex]
                                }
                            }
                        },
                        formatter: function(params) {
                            return '12'
                        },
                        data: grayBar
                    }

                ]
            }
            this.initEcharts('alarmBarEcharts', option)
        }
    }
  }
</script>

<style lang="scss" scoped>
.main-content{
  margin-top:15px;
  .todo-wrap{
      width: 100%;
      background :white;
      .todo-header{
        span{
          font-weight:bold;
          color: #323232 ;
          cursor: pointer;
        }
        .header_choose{
            color: #218FF0
        }
        .header_gang{
            display: inline-block;
            padding: 0 2%;color: #CCCCCC;
        }
        .enterTodo{
          float:right;
          width :140px;
          position: relative;
          top :-8px;
          right :-11px;
        }
      }
      .todo-list{
        .el-checkbox{
          margin-top :15px
        }
      }
  }
  .el-card{
      .el-card__body{
          padding: 5px;
      }
  }
  .no-data {
      text-align: center;
      line-height: 100%;
      color: #ccc;
      font-size: 18px;
      font-weight: bolder;
    }
}
</style>



